import api from "../index";
import { RoleDTO } from "./model.interface";

export const get = async () => await api.get("roles");
export const details = async (id: number) => await api.get(`roles/${id}`);
export const post = async (role: RoleDTO) => await api.post("roles", role);
export const put = async (id: number, role: RoleDTO) =>
  await api.put(`roles/${id}`, role);
